import { FORMATS } from '@bi/i18n';
import { AlertIcon } from '@bi/icons';
import {
  AlertState,
  AlertStatus,
  AlertWithInfo,
  DataSourceApi,
  EditAlertData,
  notifySuccess,
  useData,
} from '@bi/volkovlabs-engine-datasource';
import { EventBus } from '@grafana/data';
import { RefreshEvent } from '@grafana/runtime';
import {
  Alert,
  Button,
  ButtonGroup,
  Card,
  Collapse,
  ConfirmModal,
  Drawer,
  IconButton,
  LoadingPlaceholder,
  useStyles2,
  useTheme2,
} from '@grafana/ui';
import cronstrue from 'cronstrue';
import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { TEST_IDS } from '../../constants';
import { usePermissions } from '../../hooks';
import { convertToEditAlert, copyAlert } from '../../utils';
import { AlertOverview } from '../AlertOverview';
import { EditAlert } from '../EditAlert';
import { getTagNameByAlertState, TagItem, TagList, TagName } from '../TagList';
import { getStyles } from './AlertsList.styles';

/**
 * Properties
 */
interface Props {
  /**
   * Data Source
   *
   * @type {DataSourceApi | null}
   */
  datasource: DataSourceApi | null;

  /**
   * Event Bus
   *
   * @type {EventBus}
   */
  eventBus: EventBus;

  /**
   * Set Loading
   */
  setLoading: (loading: boolean) => void;
}

/**
 * Alerts List
 */
export const AlertsList: React.FC<Props> = ({ datasource, eventBus, setLoading }) => {
  /**
   * Styles and Theme
   */
  const theme = useTheme2();
  const styles = useStyles2(getStyles);

  /**
   * Translation
   */
  const { t, i18n } = useTranslation();

  /**
   * Permissions
   */
  const permissions = usePermissions();

  /**
   * States
   */
  const [isAdding, setAdding] = useState(false);
  const [copiedAlert, setCopiedAlert] = useState<EditAlertData | null>(null);
  const [editAlert, setEditAlert] = useState<EditAlertData | null>(null);
  const [deleteAlert, setDeleteAlert] = useState<EditAlertData | null>(null);
  const [deleting, setDeleting] = useState(false);
  const [alertOverviewId, setAlertOverviewId] = useState<string | null>(null);
  const [expanded, setExpanded] = useState<Record<string, boolean>>({});
  const [firstLoad, setFirstLoad] = useState(true);

  /**
   * Alerts
   */
  const {
    data: alerts,
    loading,
    loaded,
    update: updateAlerts,
    error,
  } = useData({
    initial: [],
    query: useCallback((datasource) => datasource.api.alerts.getAll(), []),
    datasource,
  });

  /**
   * Webhooks Count
   */
  const {
    data: webhooksCount,
    loading: webhooksCountLoading,
    loaded: webhooksCountLoaded,
  } = useData({
    initial: 0,
    query: useCallback(async (datasource) => {
      const webhooks = await datasource.api.webhooks.getAll();

      return webhooks.length;
    }, []),
    datasource,
  });

  /**
   * Alert Groups
   */
  const alertGroups = useMemo(() => {
    const alertGroupsMap = alerts.reduce((acc, alert) => {
      if (!acc.get(alert.dashboardId)) {
        acc.set(alert.dashboardId, []);
      }

      acc.set(alert.dashboardId, (acc.get(alert.dashboardId) || []).concat(alert));
      return acc;
    }, new Map<string, AlertWithInfo[]>());

    return [...alertGroupsMap].map(([dashboardId, items]) => ({
      id: dashboardId,
      title: items[0]?.dashboardTitle || t('alertingPanel.panel.alertsList.removedDashboardTitle'),
      items,
      stateCount: {
        unknown: items.filter((item) => item.state === AlertState.SCHEDULED && item.status === AlertStatus.PAUSED)
          .length,
        scheduledActive: items.filter(
          (item) => item.state === AlertState.SCHEDULED && item.status === AlertStatus.ACTIVE
        ).length,
        alerting: items.filter((item) => item.state === AlertState.ALERTING).length,
        error: items.filter((item) => item.state === AlertState.ERROR).length,
        ok: items.filter((item) => item.state === AlertState.OK).length,
      },
    }));
  }, [alerts, t]);

  /**
   * On Add Alert
   */
  const onAddAlert = useCallback(
    async (alert: EditAlertData) => {
      if (!datasource || !datasource.api) {
        return;
      }

      try {
        await datasource.api.alerts.create(alert);
        await updateAlerts();

        setAdding(false);
        notifySuccess([
          t('alertingPanel.panel.alertsList.alertCreatedTitle'),
          t('alertingPanel.panel.alertsList.alertCreatedMessage', { name: alert.title }),
        ]);

        /**
         * Expand Group for added alert
         */
        const { dashboardId: groupId } = alert;
        if (groupId) {
          setExpanded((expanded) => ({
            ...expanded,
            [groupId]: true,
          }));
        }
      } catch {}
    },
    [datasource, t, updateAlerts]
  );

  /**
   * On Update Alert
   */
  const onUpdateAlert = useCallback(
    async (alert: EditAlertData) => {
      if (!datasource || !datasource.api || !editAlert) {
        return;
      }

      try {
        await datasource.api.alerts.update(editAlert.id, alert);
        await updateAlerts();

        setEditAlert(null);
        notifySuccess([
          t('alertingPanel.panel.alertsList.alertUpdatedTitle'),
          t('alertingPanel.panel.alertsList.alertUpdatedMessage', { name: editAlert.title }),
        ]);
      } catch {}
    },
    [datasource, editAlert, t, updateAlerts]
  );

  /**
   * On Delete Alert
   */
  const onDeleteAlert = useCallback(
    async (alert: EditAlertData) => {
      if (!datasource || !datasource.api) {
        return;
      }

      try {
        setDeleting(true);
        await datasource.api.alerts.delete(alert.id);
        await updateAlerts();

        setDeleteAlert(null);
        setDeleting(false);
        notifySuccess([
          t('alertingPanel.panel.alertsList.alertDeletedTitle'),
          t('alertingPanel.panel.alertsList.alertDeletedMessage', { name: alert.title }),
        ]);
      } catch {
        setDeleting(false);
      }
    },
    [datasource, t, updateAlerts]
  );

  /**
   * On Toggle Alert Status
   */
  const onToggleAlertStatus = useCallback(
    async (alert: AlertWithInfo) => {
      if (!datasource || !datasource.api) {
        return;
      }

      try {
        const newStatus = alert.status === AlertStatus.ACTIVE ? AlertStatus.PAUSED : AlertStatus.ACTIVE;
        await datasource.api.alerts.updateStatus(alert.id, newStatus);
        await updateAlerts();

        if (newStatus === AlertStatus.ACTIVE) {
          notifySuccess([
            t('alertingPanel.panel.alertsList.alertActivatedTitle'),
            t('alertingPanel.panel.alertsList.alertActivatedMessage', { name: alert.title }),
          ]);
        } else {
          notifySuccess([
            t('alertingPanel.panel.alertsList.alertPausedTitle'),
            t('alertingPanel.panel.alertsList.alertPausedMessage', { name: alert.title }),
          ]);
        }
      } catch {}
    },
    [datasource, t, updateAlerts]
  );

  /**
   * Get Alert for overview
   */
  const alertOverview = useMemo(() => {
    return alerts.find((item) => item.id === alertOverviewId);
  }, [alertOverviewId, alerts]);

  /**
   * Refresh on dashboard update
   */
  useEffect(() => {
    /**
     * On Refresh
     */
    const subscriber = eventBus.getStream(RefreshEvent).subscribe(() => {
      updateAlerts();
    });

    return () => {
      subscriber.unsubscribe();
    };
  }, [eventBus, updateAlerts]);

  /**
   * Set Panel Loading
   */
  useEffect(() => {
    setLoading(loading || webhooksCountLoading);
  }, [loading, setLoading, webhooksCountLoading]);

  /**
   * Reset First Load
   */
  useEffect(() => {
    if (loaded && webhooksCountLoaded) {
      setFirstLoad(false);
    }
  }, [loaded, webhooksCountLoaded]);

  /**
   * Get Schedule
   */
  const getSchedule = useCallback(
    (schedule: string) => {
      try {
        return cronstrue.toString(schedule, { locale: i18n.language });
      } catch {
        return '';
      }
    },
    [i18n.language]
  );

  /**
   * On Set All Expanded
   */
  const onSetAllExpanded = useCallback(
    (isOpen: boolean) => {
      setExpanded(
        alertGroups.reduce(
          (acc, { id }) => ({
            ...acc,
            [id]: isOpen,
          }),
          {}
        )
      );
    },
    [alertGroups]
  );

  if (firstLoad && (loading || webhooksCountLoading)) {
    return (
      <div>
        <Alert severity="info" title={t('basic.wait')} data-testid={TEST_IDS.panel.alertsLoadingMessage}>
          <LoadingPlaceholder text={t('messages.loading')} />
        </Alert>
      </div>
    );
  }

  /**
   * Error
   */
  if (error) {
    return (
      <div>
        <Alert severity="error" title={t('messages.notAvailableTitle')}>
          {t('messages.notAvailableMessage')}
        </Alert>
      </div>
    );
  }

  return (
    <div className={styles.root}>
      <div className={styles.header}>
        <ButtonGroup>
          <Button
            icon="angle-double-down"
            variant="secondary"
            onClick={() => onSetAllExpanded(true)}
            tooltip={t('alertingPanel.panel.alertsList.expandAllButton')}
            disabled={alerts.length === 0}
          />
          <Button
            icon="angle-double-up"
            variant="secondary"
            onClick={() => onSetAllExpanded(false)}
            tooltip={t('alertingPanel.panel.alertsList.collapseAllButton')}
            disabled={alerts.length === 0}
          />
        </ButtonGroup>
        {permissions.alerts.add && (
          <Button
            icon="plus-circle"
            onClick={() => setAdding(true)}
            disabled={alerts.length === 0 && webhooksCount === 0}
          >
            {t('alertingPanel.panel.alertsList.addButton')}
          </Button>
        )}
      </div>

      {alertGroups.length === 0 ? (
        <Alert title={t('alertingPanel.panel.alertsList.noItemsTitle')} severity="info">
          {webhooksCount === 0
            ? t('alertingPanel.panel.alertsList.noWebhooksMessage')
            : t('alertingPanel.panel.alertsList.noItemsMessage')}
        </Alert>
      ) : (
        <>
          {alertGroups.map(({ id, title, items, stateCount }, index) => (
            <Collapse
              label={
                <div className={styles.groupHeader}>
                  {title} ({items.length})
                  <TagList
                    tags={
                      [
                        stateCount.unknown
                          ? {
                              text: `${stateCount.unknown} ${t(`alert.state.unknown`)}`,
                              name: TagName.STATE_UNKNOWN,
                            }
                          : null,
                        stateCount.scheduledActive
                          ? {
                              text: `${stateCount.scheduledActive} ${t(`alert.state.${AlertState.SCHEDULED}`)}`,
                              name: TagName.STATE_SCHEDULED,
                            }
                          : null,
                        stateCount.ok
                          ? {
                              text: `${stateCount.ok} ${t(`alert.state.${AlertState.OK}`)}`,
                              name: TagName.STATE_OK,
                            }
                          : null,
                        stateCount.alerting
                          ? {
                              text: `${stateCount.alerting} ${t(`alert.state.${AlertState.ALERTING}`)}`,
                              name: TagName.STATE_ALERTING,
                            }
                          : null,
                        stateCount.error
                          ? {
                              text: `${stateCount.error} ${t(`alert.state.${AlertState.ERROR}`)}`,
                              name: TagName.STATE_ERROR,
                            }
                          : null,
                      ].filter((item) => item !== null) as TagItem[]
                    }
                  />
                </div>
              }
              key={id}
              collapsible={true}
              isOpen={expanded[id] ?? index === 0}
              onToggle={(isOpen) =>
                setExpanded({
                  ...expanded,
                  [id]: isOpen,
                })
              }
            >
              {items.map((alert: AlertWithInfo) => {
                const schedule = getSchedule(alert.schedule);

                /**
                 * Meta
                 */
                const meta = [
                  alert.panelTitle === null ? t('alertingPanel.panel.alertsList.removedPanelTitle') : alert.panelTitle,
                  t('alertingPanel.panel.alertsList.schedule', {
                    schedule: schedule.toLowerCase(),
                  }),
                ];

                /**
                 * Tags
                 */
                const tags: TagItem[] = [
                  {
                    text: t(`alertingPanel.panel.alertsList.status.${alert.status}`),
                    name: alert.status === AlertStatus.ACTIVE ? TagName.STATUS_ACTIVE : TagName.STATUS_PAUSED,
                  },
                ];

                /**
                 * Check Alert Valid
                 */
                if (!alert.isTargetExists) {
                  tags.push({
                    text: t('alertingPanel.panel.alertsList.targetStatus.invalid'),
                    name: TagName.TARGET_STATUS_INVALID,
                  });
                }

                /**
                 * Hide state if paused and scheduled
                 */
                if (alert.state === AlertState.SCHEDULED) {
                  if (alert.status === AlertStatus.ACTIVE) {
                    tags.push({
                      text: t('alert.state.scheduled'),
                      name: TagName.STATE_SCHEDULED,
                    });
                  } else {
                    tags.push({
                      text: t('alert.state.unknown'),
                      name: TagName.STATE_UNKNOWN,
                    });
                  }
                } else {
                  tags.push({
                    text: t(`alert.state.${alert.state}`),
                    name: getTagNameByAlertState(alert.state),
                  });
                }

                return (
                  <Card
                    key={alert.id}
                    onClick={() => {
                      if (permissions.alerts.view) {
                        setAlertOverviewId(alert.id);
                      }
                    }}
                  >
                    <Card.Heading>{alert.title}</Card.Heading>
                    <Card.Meta>{meta}</Card.Meta>
                    <Card.Tags>
                      <TagList tags={tags} />
                    </Card.Tags>

                    <Card.Description>
                      <div>
                        {alert.lastTriggered && (
                          <>
                            {t('alertingPanel.panel.alertsList.lastTriggeredDate', {
                              date: new Date(alert.lastTriggered),
                              formatParams: {
                                date: FORMATS.dateWithTime,
                              },
                            })}
                          </>
                        )}
                        {alert.state === AlertState.ALERTING && !!alert.stateStartDate && (
                          <p>
                            {t('alertingPanel.panel.alertsList.startAlertingDate', {
                              date: new Date(alert.stateStartDate),
                              formatParams: {
                                date: FORMATS.dateWithTime,
                              },
                            })}
                          </p>
                        )}
                      </div>
                    </Card.Description>
                    <Card.Figure>
                      {alert.status === AlertStatus.ACTIVE ? (
                        <AlertIcon size={40} fill={theme.colors.primary.main} />
                      ) : (
                        <AlertIcon size={40} fill={theme.colors.border.medium} />
                      )}
                    </Card.Figure>
                    <Card.SecondaryActions className={styles.secondaryActions}>
                      {permissions.alerts.view && (
                        <IconButton
                          key="info-circle"
                          name="info-circle"
                          tooltip={t('alertingPanel.panel.alertsList.overviewButton')}
                          onClick={() => setAlertOverviewId(alert.id)}
                        />
                      )}
                      {permissions.alerts.changeStatus && (
                        <>
                          {alert.status === AlertStatus.ACTIVE ? (
                            <IconButton
                              name="pause"
                              aria-label=""
                              tooltip={t('alertingPanel.panel.alertsList.pauseButton')}
                              onClick={() => onToggleAlertStatus(alert)}
                            />
                          ) : (
                            <IconButton
                              name="play"
                              aria-label=""
                              tooltip={t('alertingPanel.panel.alertsList.activateButton')}
                              onClick={() => onToggleAlertStatus(alert)}
                            />
                          )}
                        </>
                      )}
                      {permissions.alerts.edit && (
                        <>
                          <IconButton
                            name="copy"
                            aria-label=""
                            tooltip={t('alertingPanel.panel.alertsList.copyButton')}
                            onClick={() => {
                              setAdding(true);
                              setCopiedAlert(convertToEditAlert(copyAlert(alert)));
                            }}
                          />
                          <IconButton
                            name="edit"
                            aria-label=""
                            tooltip={t('alertingPanel.panel.alertsList.editButton')}
                            onClick={() => {
                              setEditAlert(convertToEditAlert(alert));
                            }}
                          />
                        </>
                      )}
                      {permissions.alerts.delete && (
                        <IconButton
                          name="trash-alt"
                          aria-label=""
                          tooltip={t('alertingPanel.panel.alertsList.deleteButton')}
                          onClick={() => setDeleteAlert(alert)}
                        />
                      )}
                    </Card.SecondaryActions>
                  </Card>
                );
              })}
            </Collapse>
          ))}
        </>
      )}

      {isAdding && (
        <Drawer
          title={t('alertingPanel.panel.alertsList.addDrawer.title')}
          onClose={() => {
            setAdding(false);
            setCopiedAlert(null);
          }}
          scrollableContent
        >
          <EditAlert
            datasource={datasource}
            onClose={() => {
              setAdding(false);
              setCopiedAlert(null);
            }}
            onSave={onAddAlert}
            initial={copiedAlert || undefined}
          />
        </Drawer>
      )}

      {!!editAlert && (
        <Drawer
          title={t('alertingPanel.panel.alertsList.editDrawer.title')}
          onClose={() => setEditAlert(null)}
          scrollableContent
        >
          <EditAlert
            datasource={datasource}
            onClose={() => setEditAlert(null)}
            onSave={onUpdateAlert}
            initial={editAlert}
          />
        </Drawer>
      )}
      {!!deleteAlert && (
        <ConfirmModal
          isOpen={true}
          title={t('alertingPanel.panel.alertsList.deleteConfirmModal.title')}
          body={t('alertingPanel.panel.alertsList.deleteConfirmModal.body', { name: deleteAlert.title })}
          confirmText={
            deleting
              ? t('alertingPanel.panel.alertsList.deleteConfirmModal.confirmButtonLoading')
              : t('alertingPanel.panel.alertsList.deleteConfirmModal.confirmButton')
          }
          onDismiss={() => setDeleteAlert(null)}
          onConfirm={() => onDeleteAlert(deleteAlert)}
        />
      )}
      {!!alertOverview && (
        <Drawer
          title={t('alertingPanel.panel.alertsList.overviewDrawer.title', { name: alertOverview.title })}
          subtitle={getSchedule(alertOverview.schedule)}
          onClose={() => setAlertOverviewId(null)}
          scrollableContent
        >
          <AlertOverview
            datasource={datasource}
            onClose={() => setAlertOverviewId(null)}
            alert={alertOverview}
            loading={loading}
          />
        </Drawer>
      )}
    </div>
  );
};
