import { useAlertConditions } from '@bi/volkovlabs-alerting-panel/hooks';
import { isEvaluationType, isGlobalAlertCondition } from '@bi/volkovlabs-alerting-panel/utils';
import {
  Alert,
  AlertAnnotationMode,
  AlertConditionType,
  AlertStatus,
  AlertTimeRangeSource,
  DataSourceApi,
  EditAlertData,
  EvaluationType,
  useData,
} from '@bi/volkovlabs-engine-datasource';
import { cx } from '@emotion/css';
import {
  Button,
  FieldSet,
  InlineField,
  InlineFieldRow,
  Input,
  RadioButtonGroup,
  RelativeTimeRangePicker,
  Select,
  useStyles2,
} from '@grafana/ui';
import { isValidCron } from 'cron-validator';
import cronstrue from 'cronstrue';
import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { v4 as uuidv4 } from 'uuid';

import { ALERT_TIME_RANGE_SOURCE_OPTIONS, ANNOTATION_OPTIONS, LABEL_WIDTH } from '../../constants';
import { AlertConditions } from '../AlertConditions';
import { getStyles } from './EditAlert.styles';

/**
 * Properties
 */
interface Props {
  /**
   * Data Source Api
   *
   * @type {DataSourceApi | null}
   */
  datasource: DataSourceApi | null;

  /**
   * On Close
   */
  onClose: () => void;

  /**
   * On Save
   */
  onSave: (data: EditAlertData) => Promise<void>;

  /**
   * Initial
   */
  initial?: EditAlertData;
}

/**
 * Alert Details
 */
export const EditAlert: React.FC<Props> = ({ datasource, onSave, onClose, initial }) => {
  /**
   * Styles
   */
  const styles = useStyles2(getStyles);

  /**
   * Translation
   */
  const { t, i18n } = useTranslation();

  /**
   * Alert
   */
  const [alert, setAlert] = useState<EditAlertData>(
    initial || {
      annotations: AlertAnnotationMode.DISABLED,
      dashboardId: null,
      id: '',
      panelId: null,
      schedule: '*/5 * * * *',
      status: AlertStatus.ACTIVE,
      timeRangeSource: AlertTimeRangeSource.DASHBOARD,
      title: '',
      webhookIds: [],
      conditions: [
        {
          id: uuidv4(),
          type: AlertConditionType.GLOBAL,
          variables: {},
          repeat: null,
          evaluationType: EvaluationType.THRESHOLDS,
          evaluation: {
            type: EvaluationType.THRESHOLDS,
            colors: [],
          },
        },
      ],
    }
  );

  /**
   * Saving
   */
  const [saving, setSaving] = useState(false);

  /**
   * Dashboards
   */
  const { data: dashboards, loading: isDashboardLoading } = useData({
    initial: [],
    query: useCallback((datasource) => datasource.api.grafana.getDashboards(), []),
    datasource,
  });

  /**
   * Dashboard Options
   */
  const dashboardOptions = useMemo(() => {
    return dashboards.map((dashboard) => ({
      value: dashboard.uid,
      label: dashboard.title,
    }));
  }, [dashboards]);

  /**
   * Dashboard Info
   */
  const { data: dashboardInfo, loading: isDashboardInfoLoading } = useData({
    initial: null,
    query: useCallback(
      async (datasource) => {
        if (alert.dashboardId) {
          return datasource.api.grafana.getDashboardInfo(alert.dashboardId);
        }
        return null;
      },
      [alert.dashboardId]
    ),
    datasource,
  });

  /**
   * Webhooks
   */
  const { data: webhooks, loading: isWebhooksLoading } = useData({
    initial: [],
    query: useCallback(async (datasource) => {
      return datasource.api.webhooks.getAll();
    }, []),
    datasource,
  });

  /**
   * Default Webhook
   */
  const { data: defaultWebhook, loading: isDefaultWebhookLoading } = useData({
    initial: null,
    query: useCallback(
      async (datasource) => {
        if (!alert.webhookIds.length) {
          return datasource.api.webhooks.getDefault();
        }
        return null;
      },
      [alert.webhookIds.length]
    ),
    datasource,
  });

  /**
   * Panel Options
   */
  const panelOptions = useMemo(() => {
    if (!dashboardInfo) {
      return [];
    }

    return dashboardInfo.panels
      .filter((panel) => panel.type !== 'row')
      .map((panel) => ({
        value: panel.id,
        label: `${panel.title || t('alertingPanel.panel.editAlert.panelOptions.untitled')} [${panel.type}]`,
      }));
  }, [dashboardInfo, t]);

  /**
   * Web Hook Options
   */
  const webHookOptions = useMemo(
    () =>
      webhooks.map((webhook) => ({
        value: webhook.id,
        label: webhook.title,
      })),
    [webhooks]
  );

  /**
   * Annotation Options
   */
  const annotationOptions = useMemo(() => {
    return ANNOTATION_OPTIONS(t);
  }, [t]);

  /**
   * Schedule Description
   */
  const scheduleDescription = useMemo(() => {
    try {
      if (isValidCron(alert.schedule, { seconds: true, alias: true })) {
        return cronstrue.toString(alert.schedule, { locale: i18n.language });
      }
      return '';
    } catch {
      return '';
    }
  }, [alert.schedule, i18n.language]);

  /**
   * Alert Conditions
   */
  const {
    value: conditions,
    onChange: onChangeConditions,
    initialized: conditionsAvailable,
    thresholds,
  } = useAlertConditions({ alert, dashboard: dashboardInfo });

  /**
   * Is Valid
   */
  const isValid = useMemo(() => {
    if (!alert.title || !alert.panelId || !alert.schedule || !scheduleDescription) {
      return false;
    }

    if (alert.webhookIds.length === 0 && !defaultWebhook) {
      return false;
    }

    return conditions.every((condition) => {
      if (isGlobalAlertCondition(condition)) {
        const evaluation = condition.evaluation;

        if (isEvaluationType(evaluation, EvaluationType.PATTERN)) {
          return evaluation.pattern.trim().length > 0;
        }

        /**
         * Validate if variables set
         */
        if (Object.keys(condition.variables || {}).length > 0) {
          for (const variable of Object.values(condition.variables || {})) {
            if (!variable.value) {
              return false;
            }

            if (Array.isArray(variable.value) && !variable.value.length) {
              return false;
            }
          }
        }

        return true;
      }

      const evaluation = condition.evaluation;

      if (evaluation && isEvaluationType(evaluation, EvaluationType.PATTERN)) {
        return evaluation.pattern.trim().length > 0;
      }

      return true;
    });
  }, [
    alert.panelId,
    alert.schedule,
    alert.title,
    alert.webhookIds.length,
    conditions,
    defaultWebhook,
    scheduleDescription,
  ]);

  /**
   * Time Range Source Options
   */
  const timeRangeSourceOptions = useMemo(() => {
    return ALERT_TIME_RANGE_SOURCE_OPTIONS(t);
  }, [t]);

  return (
    <>
      <FieldSet>
        <InlineField
          label={t('alertingPanel.panel.editAlert.titleLabel')}
          grow={true}
          labelWidth={LABEL_WIDTH}
          required={true}
        >
          <Input
            onChange={(event) => {
              setAlert({
                ...alert,
                title: event.currentTarget.value,
              });
            }}
            value={alert.title}
          />
        </InlineField>

        <InlineField
          label={t('alertingPanel.panel.editAlert.scheduleLabel')}
          labelWidth={LABEL_WIDTH}
          grow={true}
          required={true}
        >
          <Input
            onChange={(event) => {
              setAlert({
                ...alert,
                schedule: event.currentTarget.value,
              });
            }}
            value={alert.schedule}
            placeholder={t('alertingPanel.panel.editAlert.schedulePlaceholder')}
          />
        </InlineField>
        <InlineField label={''} labelWidth={LABEL_WIDTH} transparent={true} grow={true} shrink={true}>
          <div
            className={cx(styles.helpText, {
              [styles.helpTextInvalid]: !scheduleDescription,
            })}
          >
            {scheduleDescription || t('alertingPanel.panel.editAlert.invalidSchedule')}
          </div>
        </InlineField>
      </FieldSet>

      <FieldSet label={t('alertingPanel.panel.editAlert.targetLabel')}>
        <InlineField
          label={t('alertingPanel.panel.editAlert.dashboardLabel')}
          grow={true}
          labelWidth={LABEL_WIDTH}
          required={true}
        >
          <Select
            onChange={(event) => {
              setAlert({
                ...alert,
                dashboardId: event.value!,
                panelId: null,
              });
            }}
            value={alert.dashboardId}
            options={dashboardOptions}
            isLoading={isDashboardLoading}
          />
        </InlineField>

        <InlineField
          label={t('alertingPanel.panel.editAlert.panelLabel')}
          grow={true}
          labelWidth={LABEL_WIDTH}
          required={true}
        >
          <Select
            onChange={(event) => {
              setAlert({
                ...alert,
                panelId: event.value!,
              });
            }}
            value={alert.panelId}
            options={panelOptions}
            isLoading={isDashboardInfoLoading}
          />
        </InlineField>
      </FieldSet>

      <FieldSet label={t('alertingPanel.panel.editAlert.timeRangeLabel')}>
        <InlineField
          label={t('alertingPanel.panel.editAlert.timeRangeSourceLabel')}
          labelWidth={LABEL_WIDTH}
          required={true}
        >
          <RadioButtonGroup
            options={timeRangeSourceOptions}
            onChange={(value) => {
              setAlert({
                ...alert,
                timeRangeSource: value,
                timeRange:
                  value === AlertTimeRangeSource.DASHBOARD
                    ? undefined
                    : {
                        from: 600,
                        to: 0,
                      },
              });
            }}
            value={alert.timeRangeSource}
          />
        </InlineField>

        {alert.timeRangeSource === AlertTimeRangeSource.CONFIGURED && alert.timeRange && (
          <InlineField
            label={t('alertingPanel.panel.editAlert.timeRangeLabel')}
            labelWidth={LABEL_WIDTH}
            required={true}
          >
            <RelativeTimeRangePicker
              timeRange={alert.timeRange}
              onChange={(event) => {
                setAlert({
                  ...alert,
                  timeRange: event,
                });
              }}
            />
          </InlineField>
        )}

        {alert.timeRangeSource === AlertTimeRangeSource.DASHBOARD && dashboardInfo?.time && (
          <InlineFieldRow>
            <InlineField label={t('alertingPanel.panel.editAlert.timeRange.from')} labelWidth={LABEL_WIDTH} disabled>
              <Input value={dashboardInfo?.time.from}></Input>
            </InlineField>
            <InlineField label={t('alertingPanel.panel.editAlert.timeRange.to')} labelWidth={LABEL_WIDTH} grow disabled>
              <Input value={dashboardInfo?.time.to}></Input>
            </InlineField>
          </InlineFieldRow>
        )}
      </FieldSet>

      {!!conditions && (
        <AlertConditions
          value={conditionsAvailable ? conditions : []}
          onChange={onChangeConditions}
          datasource={datasource}
          dashboard={dashboardInfo}
          timeRange={alert.timeRangeSource === AlertTimeRangeSource.CONFIGURED ? alert.timeRange : undefined}
          editable={conditionsAvailable}
          thresholds={thresholds}
        />
      )}

      <FieldSet label={t('alertingPanel.panel.editAlert.actionLabel')}>
        <InlineField
          label={t('alertingPanel.panel.editAlert.webhooksLabel')}
          labelWidth={LABEL_WIDTH}
          grow={true}
          invalid={alert.webhookIds.length > 0 ? false : !defaultWebhook}
          error={t('alertingPanel.panel.editAlert.noWebhooksError')}
        >
          <Select
            onChange={(event) => {
              const options = Array.isArray(event) ? event : [event!];
              setAlert({
                ...alert,
                webhookIds: options.map(({ value }) => value).filter((value) => !!value),
              });
            }}
            value={alert.webhookIds}
            options={webHookOptions}
            isMulti={true}
            isLoading={isWebhooksLoading || isDefaultWebhookLoading}
            placeholder={
              defaultWebhook
                ? t('alertingPanel.panel.editAlert.webhooksPlaceholder', { name: defaultWebhook.title })
                : t('alertingPanel.panel.editAlert.webhooksNoDefaultMessage')
            }
          />
        </InlineField>
        <InlineField label={t('alertingPanel.panel.editAlert.annotationsLabel')} labelWidth={LABEL_WIDTH} grow={true}>
          <RadioButtonGroup
            options={annotationOptions}
            onChange={(value) => {
              setAlert((alert) => ({
                ...alert,
                annotations: value,
              }));
            }}
            value={alert.annotations}
          />
        </InlineField>
      </FieldSet>

      <footer className={styles.footer}>
        <Button variant="secondary" onClick={onClose}>
          {t('alertingPanel.panel.editAlert.closeButton')}
        </Button>
        <Button
          onClick={async () => {
            setSaving(true);
            await onSave({
              ...(alert as Alert),
              conditions,
            });
            setSaving(false);
          }}
          disabled={!isValid || saving}
        >
          {t('alertingPanel.panel.editAlert.saveButton')}
        </Button>
      </footer>
    </>
  );
};
